.WebVideo {
    object-fit:fill;
    width: 100%;
    height: 100vh;
    position: absolute;
    z-index: -1;
  }
  
  .main-container {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0);
    object-fit: contain;
    overflow: hidden;
  }

  .main-container > h1{
    color:rgba(198, 198, 198, 0.618);
    font-size: 65px;
    margin-top: -200px;
    margin-bottom: 60px;
    position: relative;
    text-align: center;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif
  }

  .main-container > p{
    margin-top: 8px;
    color: white;
    font-size: 32px;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }

  .welcome-web {
    opacity: 0;
    font-size: 90px;
    animation-name: fade-in;
    color:rgba(255, 193, 35, 1);
    animation-delay: 0.3s;
    animation-duration: 4.5s;
    animation-fill-mode: forwards;
    font-weight: bold;
    text-align: center;
    margin-bottom: -20px;
    margin-top: -120px;
    margin-bottom: 200px;
  }
  
  @keyframes fade-in {
    from { opacity: 0; }
    to { opacity: 0.6; }
  }

  .main-btns{
    margin-top: -100px;
  }

  /*----------------Mobile-------------*/
  .main-mobile-container {
    height: 90vh;
    overflow-y: auto;
    overflow-x: hidden;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0);
    object-fit: contain;
    overflow: hidden;
    background-position: center top;
  }

  .typewriter-container-mobile {
    font-size: 40px;
    color:rgba(255, 193, 35, 0.429);
    font-weight: bold;
    text-align: center;
    margin-top: -120px;
    margin-bottom: 200px;
    width:100%;
  }
  .welcome-mobile {
    opacity: 0;
    font-size: 40px;
    animation-name: fade-in;
    color:rgb(1, 1, 4);
    animation-delay: 0.8s;
    animation-duration: 4.5s;
    animation-fill-mode: forwards;
    font-weight: bold;
    text-align: center;
    margin-top: -120px;
    margin-bottom: 200px;
    width:100%;
  }

  .MobileVideo {
    object-fit: fixed;
    width: 100%;
    height: 120%;
    position: absolute;
    z-index: -1;
  }
  
  .main-mobile-container > p{
    margin-top: 8px;
    color: white;
    font-size: 32px;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }
